* {
	box-sizing: border-box;
}

img {
	max-width: 100%;
	height: auto;
	display: block;
}

a {
	text-decoration: none;
	color: inherit;
}

body {
	font-family: 'Ubuntu', sans-serif;
	color: #888888;
	font-size: 14px;
	line-height: 1.64;
	font-weight: 300;
	background: #fff;
}

.wrapper {
	max-width: 1200px;
	margin: 0 auto;
	padding: 0 15px;
}

.clearfix::after {
	content: "";
	display: block;
	clear: both;
}

section {
	padding: 30px 0;
}

.heading {
	font-size: 35px;
	color: #000;
	font-weight: 300;
	padding: 10px 0 20px;
	line-height: 1.2;
}

/* Nav */
nav {
}

nav .wrapper {
}

.nav__inner {
	padding: 20px 0;
	position: relative;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.logo {
	display: block;
}

.logo__img {
	display: inline-block;
	vertical-align: middle;
}

.logo span {
	display: inline-block;
	vertical-align: middle;
	font-size: 27px;
	font-weight: 500;
	color: #333333;
	margin-left: 5px;
}

.logo sub {
	font-size: 10px;
	text-transform: uppercase;
}

.nav__menu {
	font-size: 0;
	margin-left: auto;
}

.nav__menu li {
	display: inline-block;
	vertical-align: top;
	font-size: 15px;
}

.nav__menu li a {
	display: block;
	color: #333333;
	padding: 15px;
}

.nav__menu li.dropdown {
}

.nav__menu li.dropdown ul {
	display: none;
	/* visibility: hidden; */
	position: absolute;
	top: 100%;
	background: rgba(255, 255, 255, 1);
	width: auto;
	margin: 0 auto;
	z-index: 999;
	
}

.nav__menu li.dropdown ul li {
	display: block;
}

.nav__menu li.dropdown ul li a {
	display: block;
	padding: 10px 15px;
}

.nav__menu li.dropdown ul li:hover {
	background: #f6f8fa;
}

.fa-angle-down {
	margin-left: 5px;
}

.nav__menu li a.active {
	color: #1d9d73;
}

.nav__menu li a:hover {
	color: #1d9d73;
}

.menuToggle {
	cursor: pointer;
	padding: 15px;
	display: none;
	margin-left: auto;
}

.fa.fa-bars {
	font-size: 22px;
	color: #333333;
}

.search__form {
	width: auto;
	overflow: hidden;
	position: relative;
}

.search__label {
	cursor: pointer;
	width: 30px;
	position: absolute;
	right: 0;
}

.fa-search {
	padding: 5px;
}

/* когда ставлю width: 0;,
чтобы рядом стояли меню и форма поиска, 
тогда меню резко отскакивает влево и это некрасиво.
Не получилось плавно отодвигать меню 
и пришлось оставить растояние между ними
и это смотриться нелепо */
.search__input {
	width: auto;
	opacity: 0; 
  transform: translateX(100vw);
	float: right;
	border-radius: 10px;
	border: 1px solid #888;
	outline: none;
	padding: 5px 5px 5px 35px; 
	transition: opacity 1s, transform 1s; 
}

.search__input:focus {
	box-shadow: inset 0px 0px 3px rgba(0, 0, 0, .3);
}

.search__input.show {
	width: 100%;
	opacity: 1; 
  transform: translateX(0px);
  
}

.fa {
	font-size: 18px;
	color: #1d9d73;
}
  
/* Header*/
header {
	background: url('../img/header.jpg');
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
	position: relative;
	overflow: hidden;
}

.user__form {
	position: fixed;
	right: 0;
	top: 50%;
	cursor: pointer;
	z-index: 9;
}

header .wrapper {
	display: flex;
	flex-wrap: wrap;
	min-height: 620px;
}

.header__inner {
	margin: auto;
	text-align: center;
}

.header__text {
	color: #fff;
}

.header__text h1 {
	font-size: 45px;
	font-weight: 300;
	line-height: 1.2;
	transition: all 1s;
}

.header__text h2 {
	font-size: 55px;
	font-weight: 300;
	line-height: 1.2;
	transition: all 1s;
}

.header__buttons {
	padding-top: 25px;
}

.header__buttons .btn {
	display: inline-block;
	font-size: 18px;
	color: #FFF;
	padding: 10px 52px;
	border-radius: 20px;
	margin: 0 15px;
}

.header__buttons .btn__green {
	border: 1px solid #1d9d73;
	background: #1d9d73;
}
.header__buttons .btn__green:hover {
	border: 1px solid #fff;
	background: transparent;
}
.header__buttons .btn__transparent {
	border: 1px solid #fff;
	background: transparent;
	transition: all .5s;
}
.header__buttons .btn__transparent:hover {
	border: 1px solid #1d9d73;
	background: #1d9d73;
}
/* Present */
.present {
	text-align: center;
}

.present .wrapper {
}

.present p {
	padding-bottom: 10px;
}

.present__cards {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin: 0 -15px;
}

.present__card {
	width: calc((100% / 3) - 30px);
	margin: 15px;
	word-wrap: break-word;
	display: flex;
	flex-direction: column;
}

.present__card img {
	width: 100%;
}

.present__card a {
	display: block;
	position: relative;
}

.present__card a::before {
	content: "";
	display: block;
	position: absolute;
	width: 100%;
	height: 100%;
}

.present__card:hover a::before {
	box-shadow: inset 0 0 0 10px rgba(29, 157, 115, .35);
}

.present__card h4 {
	font-size: 20px;
	color: #1d9d73;
	padding: 15px 0;
	text-align: center;
}

.present__card p {
	margin-top: auto;
}

/* News */
.news {
	background: #f6f8fa;
}

.news .wrapper {
}

.news__cards {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin: 0 -15px;
}

.news__card {
	width: calc((100% / 3) - 30px);
	margin: 15px;
	display: flex;
	flex-wrap: wrap;
}

.news__card_img {
	width: 20%;
}

.news__card_img img {
	margin: 0 auto;
}

.news__card_text {
	width: calc(80% - 10px);
	margin-left: 10px;
	display: flex;
	flex-direction: column;
}

.news__card h4 {
	font-size: 20px;
	color: #333;
	line-height: 1.2;
	padding: 10px 0;
}

.news__card p {
	padding-bottom: 10px;
}

.news__card a {
	display: block;
	color: #1d9d73;
	font-size: 15px;
	margin-top: auto;
}

.fa-angle-right {
	margin-left: 5px;
	vertical-align: middle;
}

/* Discover*/
.discover {
}

.discover .wrapper {
}

.discover__inner {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -15px;
}

.discover__phone {
	width: calc((100% / 12 * 5) - 30px);
	margin: 0 15px;
}

.discover__text {
	width: calc((100% / 12 * 7) - 30px);
	margin: 0 15px;
	padding: 30px 60px;
}

.discover__list {
	padding: 20px 0;
}

.discover__list li {
	margin: 10px 0;
}

.discover__list li img {
	display: inline-block;
	vertical-align: middle;
}

.discover__list li a {
	display: inline-block;
	padding: 10px 20px;
	font-size: 20px;
	color: #1d9d73;
}

/* Promo */
.promo {
	background: #1fa87a;
	background: linear-gradient(to right, #1fa87a 1%, #1b936c 100%);
	padding: 0;
}

.promo .wrapper {
	max-width: 2048px;
	margin: 0 auto;
	padding: 0 15px;
}

.promo__inner {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -15px;
	justify-content: center;
}

.promo__item {
	width: calc((100% / 3) - 30px);
	padding: 0 15px;
	word-wrap: break-word;
	text-align: center;
}

.promo__item-left {
	background: #1fa87a;
}

.promo__item-middle {
	background: #1d9d73;
}

.promo__item-right {
	background: #1b936c;
}

.item__box {
	padding: 100px 20px 55px;
	max-width: 400px;
	margin: 0 auto;
}

.item__box img {
	margin: 0 auto;
}

.item__box h4 {
	font-size: 25px;
	color: #fff;
	padding: 10px 0;
}

.item__box p {
	color: #FFF;
}

/* Blog */
.blog {
	text-align: center;
}

.blog .wrapper {
}

.blog__cards {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -15px;
	justify-content: center;
}

.blog__card {
	width: calc((100% / 3) - 30px);
	margin: 15px;
}

.blog__card h4 {
	font-size: 20px;
	color: #333;
	line-height: 1.2;
	padding: 10px 0;
}

.blog__card p {
	padding-bottom: 10px;
}

.blog__meta {
	padding: 10px 0;
}

.meta__item {
	margin: 0 10px;
}

.btn {
	display: inline-block;
	font-size: 18px;
	padding: 10px 50px;
	border: none;
	border-radius: 20px;
	margin: 20px 0;
}

.btn__green {
	background: #1d9d73;
	color: #FFF;
}

/* Stats */
.stats {
	background: url('../img/stats/stats_bg.jpg');
	background-position: center top;
	background-repeat: no-repeat;
	background-size: cover;
}

.stats .wrapper {
	min-height: 620px;
}

.stats__items {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -15px;
	min-height: inherit;
	justify-content: center;
	align-items: center;
	align-content: center;
	text-align: center;
}

.stats__item {
	width: calc((100% / 4) - 30px);
	margin: 15px;
}

.stats__item-icon {
	margin: 0 auto;
}

.stats__item-number {
	font-size: 45px;
	color: #1d9d73;
	line-height: 1.2;
	padding: 10px 0;
}

.stats__item-text {
	font-size: 20px;
	color: #333333;
	line-height: 1.2;
}

/* Packages */
.packages {
	text-align: center;
}

.packages .wrapper {
}

.packages__cards {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -15px;
	justify-content: center;
}

.packages__card {
	width: calc((100% / 3) - 30px);
	margin: 15px;
	background: #f6f8fa;
	padding: 25px 0;
}

.packages__card h4 {
	font-size: 20px;
	color: #333;
	line-height: 1.2;
	padding: 10px 0;
}

.packages__card .price {
	padding: 5px 0;
	background: #1d9d73;
	color: #fff;
	margin: 15px 0;
}

.packages__card .price sup {
	font-size: 24px;
}

.packages__card .price span {
	font-size: 45px;
}

.packages__card .price sub {
	font-size: 15px;
}

.packages__card ul {
	text-align: left;
	display: inline-block;
}

.packages__card ul li {
	padding: 8px 0;
}

.packages__card ul li i.fa {
	font-size: 14px;
	margin-right: 10px;
}

.packages__card ul li i.fa-times {
	color: #888888;
}

/* Slider */
.slider {
	background: #f6f8fa;
}

.slider .wrapper {
}

.slider__inner {
	position: relative;
}

.slider__items {
	text-align: center;
}

.slider__item {
}

.slider__item:focus {
	outline: 0;
}

.slider__item img {
	display: inline-block;
	line-height: 0;
	border-radius: 50%;
}

.slider__item p {
	padding: 10px 0;
	width: calc((100% / 12 * 10) - 30px);
	margin: 0 auto;
}

.slider__item h4 {
	font-size: 20px;
	color: #333;
	line-height: 1.2;
	padding: 10px 0;
}

.slider__item span {
	font-size: 15px;
	color: #1d9d73;
}

.slider__arrow {
	position: absolute;
	top: 50%;
	margin-top: -12px;
	z-index: 1;
	opacity: .8;
	transition: all 1s;
}

.slider__arrow:hover {
	opacity: 1;
	cursor: pointer;
}

.slider__arrow-prev {
	left: 0;
}

.slider__arrow-next {
	right: 0;
}

.slider__arrow i.fa {
	font-size: 24px;
	color: #333;
}

.slider__arrow:hover i.fa {
	color: #1d9d73;
}

/* Contacts */
.contacts {
	text-align: center;
}

.contacts .wrapper {
}

.contacts__inner {
}

.contacts h4 {
	font-size: 35px;
	line-height: 1.2;
	color: #000;
	padding: 10px 0;
}

.contacts p {
	font-size: 14px;
	padding: 10px 0;
}

.contacts a.tel {
	font-size: 20px;
	color: #333333;
	display: inline-block;
	padding: 5px 0;
}

.contacts a.tel::before {
	content: url('../img/telephone.png');
	width: 38px;
	height: 40px;
	display: inline-block;
	vertical-align: middle;
	margin-right: 10px;
}

.contacts .time {
	display: block;
	padding: 5px 0;
}

.contacts .icons {
}

.contacts .icons a {
	padding: 15px;
}
.contacts .icons a:hover {
	color: #888;
}
.contacts .copyright {
	padding: 10px 0;
}

.contacts .copyright img {
	display: inline-block;
	vertical-align: middle;
}

.contacts .copyright span {
	margin-left: 10px;
}

@media screen and (max-width:992px) {
	.menuToggle {
		display: block;
	}

	.nav__menu {
		display: none;
		/* text-align: center; */
		position: absolute;
		background: rgba(255, 255, 255, 1);
		top: 100%;
		margin: 0 -15px;
		width: calc(100% + 30px);
		z-index: 999;
		padding: 0 44px;
	}

	.nav__menu li {
		display: block;
		transition: all 1s;
	}

	.nav__menu li:hover {
		background: #f6f8fa;
	}

	.nav__menu li.dropdown ul {
		display: none;
		/* visibility: hidden; */
		position: static;
		top: 100%;
		background: none;
		width: 100%;
		margin: 0 auto;
	}

	.nav__menu li.dropdown ul li {
		padding-left: 40px;
	}

	.nav__menu li.dropdown:hover {
		background: none;
	}

	.nav__menu li.dropdown .fa-angle-down {
		transition: all 1s;
	}

	.nav__menu li.dropdown:hover .fa-angle-down {
		margin-left: 40px;
	}

	.present__card {
		width: calc((100% / 1) - 30px);
	}

	.present__card a {
		margin: 0 auto;
	}

	.news__card {
		width: calc((100% / 2) - 30px);
	}

	.packages__card {
		width: calc((100% / 1) - 30px);
	}

	.discover__phone {
		width: calc((100% / 1) - 30px);
		margin: 0 15px;
	}

	.discover__phone img {
		margin: 0 auto;
	}

	.discover__text {
		width: calc((100% / 1) - 30px);
		margin: 0 15px;
		padding: 20px;
	}

	.promo__item {
		width: 100%;
		padding: 0 15px;
		word-wrap: break-word;
		text-align: center;
	}

	.blog__card {
		width: calc((100% / 1) - 30px);
	}

	.blog__card img {
		margin: 0 auto;
	}

	.stats {
		background: #DDDED6;
	}
.stats .wrapper {
	min-height: auto;
}
.stats__items{
	padding: 20px 0;
}
	.stats__item {
		width: calc((100% / 2) - 30px);
		margin: 15px;
	}

}

@media screen and (max-width:720px) {
	.news__card {
		text-align: center;
		width: auto;
	}

	.news__card_img {
		width: 100%;
	}

	.news__card_text {
		width: auto;
		margin-left: 0;
	}

	.news__card a {
		margin: 10px 0;
	}

}

@media screen and (max-width:576px) {
	.logo__img {
		padding: 15px 0;
	}

	.logo span {
		display: none;
	}

	.header__text h1 {
		font-size: 35px;
	}

	.header__text h2 {
		font-size: 45px;
	}

	.header__buttons .btn {
		display: block;
	}

	.header__buttons .btn__green {
		margin-bottom: 15px;
	}

	.discover__text {
		padding: 20px 0;
	}

	.discover__list li a {
		padding: 10px;
		font-size: 16px;
	}

	.item__box {
		padding: 50px 20px;
	}

	.stats__item {
		width: calc((100% / 1) - 30px);
		margin: 15px;
	}

}